import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-fixed-plugin',
  templateUrl: './fixed-plugin.component.html',
  styleUrls: ['./fixed-plugin.component.scss']
})
export class FixedPluginComponent implements OnInit {
  public sidebarColor: string;
  public sidebarActiveColor: string;

  public state: boolean;

  constructor() {
    this.sidebarColor = 'white';
    this.sidebarActiveColor = 'danger';
    this.state = true;
  }

  ngOnInit(): void {

  }

  changeSidebarColor(color: string): void {
    const sidebar = document.querySelector('.sidebar') as HTMLElement;

    this.sidebarColor = color;
    if (sidebar !== undefined)
    {
      sidebar.setAttribute('data-color', color);
    }
  }

  changeSidebarActiveColor(color: string): void {
    const sidebar = document.querySelector('.sidebar') as HTMLElement;

    this.sidebarActiveColor = color;
    if (sidebar !== undefined)
    {
      sidebar.setAttribute('data-active-color', color);
    }
  }

}
