import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  { path: 'users', loadChildren: () => import('./users/users.module').then(module => module.UsersModule) },
  { path: 'roles', loadChildren: () => import('./roles/roles.module').then(module => module.RolesModule) },
  { path: 'permissions', loadChildren: () => import('./permissions/permissions.module').then(module => module.PermissionsModule) },
  { path: 'brands', loadChildren: () => import('./brands/brands.module').then(module => module.BrandsModule) },
  { path: 'categories', loadChildren: () => import('./categories/categories.module').then(module => module.CategoriesModule) },
  { path: 'products', loadChildren: () => import('./products/products.module').then(module => module.ProductsModule) },
  { path: 'orders', loadChildren: () => import('./orders/orders.module').then(module => module.OrdersModule) },
  { path: 'promocodes', loadChildren: () => import('./promo-codes/promo-codes.module').then(module => module.PromoCodesModule) },
  { path: '**', redirectTo: 'users', pathMatch: 'full' },
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule { }
